module.exports = ({ services, components }) => () => {

    const $avatarMaker = document.createElement('div');
    $avatarMaker.className = 'avatar-maker';

    const $dropzone = components.dropzone(services.tags.insertImageFile);
    $avatarMaker.append($dropzone);

    $dropzone.append(
        components.controlPanel(),
        components.tagList()
    );

    return $avatarMaker;

};
