module.exports = ({ components }) => () => {

    const { naming, images, laminating, multiples, badges } = components.tips;
    const sequence = [naming, images, laminating, multiples, badges];

    const $tips = document.createElement('div');
    $tips.className = 'tips';

    sequence.forEach(render => {
        const $tip = render();
        $tip.className = 'tip';
        const $heading = document.createElement('div');
        $heading.className = 'heading';
        $heading.textContent = $tip.title;
        $tip.prepend($heading);
        $tips.append($tip);
    });

    return $tips;

};
