const wire = require('./wire');
const constants = require('./constants');

const libraries = {
    lib: require('./lib'),
    stores: require('./stores'),
    services: require('./services'),
    components: require('./components'),
    styles: require('./styles')
};

const wiring = {
    lib: 'util',
    stores: 'lib',
    services: 'context stores constants lib util',
    styles: 'services stores lib',
    components: 'context styles services stores constants lib',
};

const utilDefaults = {
    random: Math.random,
    fetch: fetch.bind(window),
    URL: window.URL,
    FileReader: window.FileReader
};

const createInstance = (name, utilOverrides = {}) => {
    const util = { ...utilDefaults, ...utilOverrides };
    const app = wire(libraries, wiring, { context, constants, util });
    app.services.state.initialise();
    app.services.keyboard.bindKeys();
    context.instances.push({ name, ...app });
    return app;
};

const context = {
    createInstance,
    instances: []
};

module.exports = window.agileavatars = context;
