module.exports = ({ components }) => ({ onSubmit }) => {

    const $gravatar = document.createElement('div');
    $gravatar.title = 'Gravatar';
    $gravatar.className = 'gravatar';

    $gravatar.append(
        components.gravatarFreetext(),
        components.gravatarFallbacks(),
        components.gravatarActions({ onSubmit })
    );

    return $gravatar;

};
