module.exports = ({ components }) => rootComponent => {

    const $container = document.createElement('div');
    const $shadow = $container.attachShadow({ mode: 'open' });

    $shadow.append(
        components.stylesContainer(),
        rootComponent
    );

    return $container;

};

/*

Appends the styles container and a given root component to a shadow dom for isolation.
This prevents external styles from clashing with internal styles.
See: https://developer.mozilla.org/en-US/docs/Web/Web_Components/Using_shadow_DOM

*/