module.exports = ({ services, util }) => (filename, imageBlob) => {

    if (!imageBlob.type.startsWith('image/')) return;

    const reader = new util.FileReader();

    reader.addEventListener('load', () => {
        const image = reader.result;
        const [name, roleName] = services.tags.parseFilename(filename);
        services.tags.insertTagWithRoleName({ name, roleName, image });
    });

    reader.readAsDataURL(imageBlob);

};
