module.exports = ({ constants, components }) => () => {

    const { modes, shapes } = constants.options;
    const { numberOption, shapeOption, keepSortedOption, showOutlineOption } = components;

    const layout = [
        modes.map(numberOption),
        shapes.map(shapeOption),
        ['size', 'spacing'].map(numberOption),
        keepSortedOption(),
        showOutlineOption()
    ];

    const groups = layout.map(option => {
        const options = [].concat(option);
        const $group = document.createElement('span');
        $group.append(...options);
        return $group;
    });

    const $optionsBar = document.createElement('div');
    $optionsBar.className = 'options-bar';
    $optionsBar.append(...groups);
    return $optionsBar;

};
