module.exports = ({ constants, services, util }) => async email => {

    const { domain } = constants.gravatar;
    const emailHash = services.gravatar.hashEmail(email);
    const url = `${domain}/${emailHash}.json`;
    const response = await util.fetch(url);
    if (!response.ok) return null;
    const data = await response.json();
    return data.entry[0];

};
