module.exports = () => onFileDropped => {

    const $dropzone = document.createElement('div');
    $dropzone.className = 'dropzone';

    $dropzone.addEventListener('dragenter', silence, false);
    $dropzone.addEventListener('dragover', silence, false);
    $dropzone.addEventListener('drop', drop(onFileDropped), false);

    return $dropzone;

};

const silence = e => {
    e.stopPropagation();
    e.preventDefault();
    return false;
};

const drop = callback => e => {
    Array.from(e.dataTransfer.files).forEach(callback);
    return silence(e);
};
