module.exports = ({ services, constants, util }) => async (email, fallback) => {

    const { domain, size } = constants.gravatar;
    const emailHash = services.gravatar.hashEmail(email);
    const url = `${domain}/avatar/${emailHash}?r=g&s=${size}&d=${fallback}`;
    const response = await util.fetch(url);
    return response.blob();

};
